	//VM 协调者调度器
	export default {
		//Model:所有的数据
		data() {
			return {
			    news:[]
			}
		},
		// 生命周期
		onLoad:function() {
			uni.showLoading({
				title:"加载中..."
			})
            uni.request({//发送页面请求
            	url: 'https://unidemo.dcloud.net.cn/api/news',//后台端口
            	method: 'GET',//接收后台传来的数据
            	data: {},
            	success: res => {
					console.log(res);
                    // 当前数组放到当前页面
					this.news = res.data;//把数据放在页面上
					uni.hideLoading();
				},
            	fail: () => {},
            	complete: () => {}
            });  
		},
		methods: {
           openinfor(e){
		   console.log(e);
